export interface ICustomIntegrationItem {
    isActivated?: boolean;
    name: string;
    description?: string;
    loadOption: {
        loadOnce: boolean;
        loadEverytime: boolean;
    };
    headCode?: string;
    bodyCode?: string;
    footerCode?: string;
    events?: Array<IEventData>;
    index?: number;
    isEasyIntegration?: boolean;
    customIcon?: string;
}
export interface IIntegrationsSettings {
    easyIntegrations?: any;
    customIntegrations: Array<ICustomIntegrationItem>;
}

export interface IEventData {
    eventName: string;
    parameters?: Array<{ 
        parameterName: string;
        parameterType?: IntegrationSelectableTypes;
        parameterObjectContent?: any; 
    }>,
    returnValue: {
        hasReturnValue: boolean;
        returnValueType?: IntegrationSelectableTypes;
        objectContent?: any;
    }; 
    eventContent: string,
    index?: number;
    integrationName?: string,
}

export type IntegrationSelectableTypes = "number" | "boolean" | "object" | "string";
